        "thermostat": { title: "Heat/Cool Thermostat", description: "This node functions as a Heat/Cool thermostat. While enable is set to true, the node will function as follows: CLG Mode will be set to true if input is greater than setpoint + CLG offset, CLG Mode will be set to false if input is less than setpoint. HTG Mode will be set to true if input is less than setpoint - HTG offset, HTG Mode will be set to false if input is greater than setpoint. If enable is false, CLG Mode and HTG Mode will both be set to false." },
        "pac-unit-control": { title: "PAC Unit Control", description: "This node functions as a Heat/Cool thermostat. While enable is set to true, the node will function as follows: CLG Mode will be set to true if input is greater than setpoint + CLG offset, CLG Mode will be set to false if input is less than setpoint. HTG Mode will be set to true if input is less than setpoint - HTG offset, HTG Mode will be set to false if input is greater than setpoint. If enable is false, CLG Mode and HTG Mode will both be set to false." },
        "PID": { title: "PID", description: "This node generates a PID output based on input parameter settings. Inputs: Enable, Setpoint, Process Variable, inP (Proportional), inI (Integral), inD (Derivative), Direction, Interval, MaxOut, MinOut, Manual, and Bias. If direction is true the PID will be a Direct loop (output increases when processVariable > setpoint), if direction is false the PID will be a Reverse loop (output increases when processVariable < setpoint). interval is the period that the PID output is re-calculated. bias value is added to output. manual will be the output value when enable is false. PID integral factor will be reset to 0 when reset transitions from false to true. 'interval units can be configured from settings. Maximum interval setting is 587 hours." },
        "toggle-duration": { title: "Toggle Duration", description: "This node accepts a pushButton input which triggers output to true for the runtime, or until pushButton transitions from false to true within the runtime period" },
        "psychrometrics": { title: "Psychrometric Calcs (Metric/SI)", description: "This node provides psychrometric calculations based on a selection of: Dry Bulb Temp, Wet Bulb Temp, Dew Point, Relative Humidity, and Pressure. Inputs and Outputs as follows: (i) Dry bulb temperature in F [IP] or C [SI]; (i) Wet bulb temperature in F [IP] or C [SI]; (i) Atmospheric pressure in Psi [IP] or Pa [SI]; (o) Humidity ratio in lb_HO lb_Air [IP] or kg_HO kg_Air [SI]; (o) Dew point temperature in F [IP] or C [SI]; (o) Relative humidity [0-1]; (o) Partial pressure of water vapor in moist air in Psi [IP] or Pa [SI]; (o) Moist air enthalpy in Btu lb [IP] or J kg [SI]; (o) Specific volume ft lb [IP] or in m kg [SI]. Input types can be selected from settings. Outputs will change based on selected input type. Unit system can be set from settings to be in: Metric/SI or Imperial/IP." },
        "energy-conversion": { title: "Energy Conversion (Wh)", description: "This node converts between Energy units watt-hour (Wh), kilowatt-hour (kWh), British Thermal Unit (Btu), calorie (cal), joule(J), and joule(kJ). Input type can be selected from settings. Outputs will change based on selected input type." },
        "power-conversion": { title: "Power Conversion (kW)", description: "This node converts between Power units kilowatt (kW), Tons of Refrigeration (tons), British Thermal Unit per hour (Btu/h), and Electrical Horsepower (hp). Input type can be selected from settings. Outputs will change based on selected input type." },
        "volume-conversion": { title: "Volume Conversion (L)", description: "This node converts between Volume units Litre (L), Cubic Metre (m), Cubic Foot (ft), and Gallon (gal). Input type can be selected from settings. Outputs will change based on selected input type." },
        "temperature-conversion": { title: "Temperature Conversion (C-> F)", description: "This node converts between Temperature units: Celsius, Fahrenheit, Kelvin, and Rankine . Input type can be selected from settings. Outputs will change based on selected input type." },
        "atmospheric-pressure": { title: "Atmospheric Pressure (Meters-> Pa)", description: "This node converts altitude to Atmospheric Pressure in various units: Pascal (Pa), Standard Atmosphere (atm), Bar (bar), and Pound-force per square inch (lbf/in2) . altitude units can be set from settings to be in: Metric/SI (Meters-> Pa), or Imperial/IP (Feet-> Psi)." },
        "pressure-conversion": { title: "Pressure Conversion (Pa)", description: "This node converts between Pressure units: Pascal (Pa), Standard Atmosphere (atm), Bar (bar), and Pound-force per square inch (lbf/in2) . Input type can be selected from settings. Outputs will change based on selected input type." },
